@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

:root {
	--text_color: #fdfffe;
	--text_secondary: #0069ff;
	--text_tertiary: #1875ff;
	--bg_color: #091133;
	--todo_bg: #091133;
	--white: #fdfffe;
	--input-bgc: #fdfffe;
	--input-text: #091133;
	--important-todo: #f6b40c;
	--done-icon: #1aff00;
	--delete-icon: #f62f0c;
}

[data-theme='dark'] {
	--text_color: #fdfffe;
	--text_secondary: #0069ff;
	--bg_color: #091133;
	--todo_bg: #0a102d;
	--input-bgc: #fdfffe;
	--input-text: #091133;
	--important-todo: #f6b40c;
	--text_tertiary: #1875ff;
	--done-icon: #1aff00;
	--delete-icon: #f62f0c;
}

[data-theme='light'] {
	--text_color: #091133;
	--text_secondary: #0069ff;
	--bg_color: #e3e7ff;
	--todo_bg: #e3e7ff;
	--input-text: #091133;
	--input-bgc: transparent !important;
	--important-todo: #0069ff;
	--text_tertiary: #1875ff;
	--done-icon: #22b214;
	--delete-icon: #f62f0c;
}

* {
	margin: 0;
	padding: 0;
	font-family: 'Raleway', sans-serif;
	-webkit-tap-highlight-color: transparent;
}

::-webkit-scrollbar {
	width: 12px;
	transition: all 0.3s;
}

::-webkit-scrollbar-track {
	background: transparent;
}

::-webkit-scrollbar-thumb {
	background: var(--input-text);
	border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
	background: var(--text_secondary);
}

html {
	overflow-y: scroll;
	scroll-behavior: smooth;
}

ul {
	position: relative;
}

li {
	list-style: none;
	width: 100%;
	position: relative;
	user-select: none;
}

body {
	background-color: var(--bg_color);
	transition: background-color 0.3s;
	display: flex;
	justify-content: center;
	padding-right: 0 !important;
}

@media (max-width: 650px) {
	body {
		color: var(--text_color);
		background-color: var(--bg_color);
		height: 80vh;
		background-size: 100% 1000%;
		transition: all 0.3s;
	}
}
