.wrapper {
	text-align: center;
}

.categories {
	display: flex;
	flex-direction: column;
	gap: 32px;
}
.section {
	display: flex;
	flex-direction: column;
	gap: 16px;
}

.items {
	display: flex;
	justify-content: center;
	gap: 20px;
}

.items label > input[type='file'] {
	width: 0px;
	height: 0px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}

.setting {
	cursor: pointer;
	background-color: transparent;
	color: var(--text_color);
	font-size: 4rem;
	transition: 0.3s;
	user-select: none;
	padding: 10px;
	border: 3px solid var(--text_color);
	border-radius: 12px;
	display: flex;
	align-items: center;
}

.setting:active {
	color: var(--text_secondary);
	border: 3px solid var(--text_secondary);
	transform: scale(1.1);
}

.hint {
	max-width: 260px;
	margin: 0 auto;
	color: var(--text_color);
	opacity: 0.65;
}

@media (hover: hover) {
	.setting:hover {
		transform: scale(1.1);
	}
}

@media (max-width: 650px) {
	.setting {
		font-size: 3rem;
	}
}
